// ----------------------------------基础布局样式----------------------------------------
.ti2 {
  text-indent: 8px;
}

// 内边距
.createPadding(@n, @i: 2) when (@i =< @n) {
  .p@{i} {
    padding: @i * 1px;
  }

  .pt@{i} {
    padding-top: @i * 1px;
  }

  .pl@{i} {
    padding-left: @i * 1px;
  }

  .pb@{i} {
    padding-bottom: @i * 1px;
  }

  .pr@{i} {
    padding-right: @i * 1px;
  }

  .ptb@{i} {
    padding-top   : @i * 1px;
    padding-bottom: @i * 1px;
  }

  .plr@{i} {
    padding-left : @i * 1px;
    padding-right: @i * 1px;
  }

  .createPadding(@n, (@i + 2));
}

.createPadding(128);

// 外边距
.createMargin(@n, @i: 2) when (@i =< @n) {
  .m@{i} {
    margin: @i * 1px;
  }

  .mt@{i} {
    margin-top: @i * 1px;
  }

  .ml@{i} {
    margin-left: @i * 1px;
  }

  .mb@{i} {
    margin-bottom: @i * 1px;
  }

  .mr@{i} {
    margin-right: @i * 1px;
  }

  .mtb@{i} {
    margin-top   : @i * 1px;
    margin-bottom: @i * 1px;
  }

  .mlr@{i} {
    margin-left : @i * 1px;
    margin-right: @i * 1px;
  }

  .createMargin(@n, (@i + 2));
}

.createMargin(128);

// 高宽
.createWH(@n, @i: 1) when (@i =< @n) {
  @name: @i*32;

  .w@{name} {
    width: @i * 32px;
  }

  .h@{name} {
    height: @i * 32px;
  }

  .createWH(@n, (@i + 1));
}

.createWH(30);

.w33 {
  width: 33%;
}

.w50 {
  width: 50%;
}

.br3 {
  border-radius: 3px;
}

// 字体
.fs0 {
  font-size: 0px;
}

.createFS(@n, @i: 12) when (@i =< @n) {
  .fs@{i} {
    font-size: @i * 1px;
  }

  .createFS(@n, (@i + 2));
}

.createFS(60);

