// tool
// 0.5px
.thin {
  position: relative;

  &::after {
    position        : absolute;
    left            : 0;
    top             : 0;
    border          : 1px solid;
    width           : 200%;
    height          : 200%;
    content         : "";
    transform       : scale(0.5);
    transform-origin: left top;
  }
}

// 用于全屏居中（容器）
.center-fullscreen {
  display        : flex;
  justify-content: center;
  align-items    : center;
  position       : absolute;
  width          : 100%;
  height         : 100%;
  top            : 0;
  left           : 0;
}

.fullscreen {
  width     : 100vw;
  height    : 100vh;
  box-sizing: border-box;
  overflow  : hidden;
}

.box {
  box-sizing: border-box;
}

.shadow {
  box-shadow: 0px 8px 16px -4px rgba(37, 48, 68, 0.16), 0px 1px 3px 0px rgba(37, 48, 68, 0.12);
}

.shadow-hover {
  &:hover {
    transition: box-shadow 0.3s;
    box-shadow: 0px 8px 16px -4px rgba(37, 48, 68, 0.16), 0px 1px 3px 0px rgba(37, 48, 68, 0.12);
  }
}

.paper {
  background: #fff;
}

//文字限制几多行后就出现省略号
.ellip1 {
  overflow     : hidden;
  text-overflow: ellipsis;
  white-space  : nowrap;
}

.ellip2 {
  display           : -webkit-box;
  overflow          : hidden;
  box-orient        : vertical;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.ellip3 {
  display           : -webkit-box;
  overflow          : hidden;
  box-orient        : vertical;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.link {
  cursor         : pointer;
  color          : @theme_color;
  text-decoration: none;
}

.mark {
  background-color: rgba(0, 0, 0, 0.2);
}

.link-disable {
  cursor         : not-allowed;
  color          : @hint_b;
  text-decoration: none;
}

.btn-tag-info {
  display         : inline-block;
  padding         : 2px 6px 2px 6px;
  color           : @theme_color;
  background-color: rgba(@theme_color, 10%);
  border-radius   : 2px;
  font-size       : 12px;
  font-weight     : normal;
}

.btn-tag-warn {
  display         : inline-block;
  padding         : 2px 6px 2px 6px;
  color           : @warn_yellow;
  background-color: rgba(@warn_yellow, 10%);
  border-radius   : 2px;
  font-size       : 12px;
  font-weight     : normal;
}

.btn-tag-error {
  display         : inline-block;
  padding         : 2px 6px 2px 6px;
  color           : @fail_red;
  background-color: rgba(@fail_red, 10%);
  border-radius   : 2px;
  font-size       : 12px;
  font-weight     : normal;
}

.btn-tag-success {
  display         : inline-block;
  padding         : 2px 6px 2px 6px;
  color           : @success_green;
  background-color: rgba(@success_green, 10%);
  border-radius   : 2px;
  font-size       : 12px;
  font-weight     : normal;
}

.btn-tag-disable {
  display         : inline-block;
  padding         : 2px 6px 2px 6px;
  color           : @disable;
  background-color: rgba(@disable, 10%);
  border-radius   : 2px;
  font-size       : 12px;
  font-weight     : normal;
}

.fadein {
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.divider {
  border-top: 1px solid hsla(0, 0%, 91%, .1);
  width     : 100%;
}